


































export default {
	data() {
		return {
			title: 'request-payment',
			loading: false,
			providerList: [],
			money:''
		}
	},
	onLoad: function(options) {
		this.money = options.money;





























	},
	methods: {
		weixinPay() {
			console.log("发起支付");
			this.loading = true;
			uni.login({
				success: (e) => {
					console.log("login success", e);
					uni.request({
						url: `https://unidemo.dcloud.net.cn/payment/wx/mp?code=${e.code}&amount=0.01`,
						success: (res) => {
							console.log("pay request success", res);
							if (res.statusCode !== 200) {
								uni.showModal({
									content: "支付失败，请重试！",
									showCancel: false
								});
								return;
							}
							if (res.data.ret === 0) {
								console.log("得到接口prepay_id", res.data.payment);
								let paymentData = res.data.payment;
								uni.requestPayment({
									timeStamp: paymentData.timeStamp,
									nonceStr: paymentData.nonceStr,
									package: paymentData.package,
									signType: 'MD5',
									paySign: paymentData.paySign,
									success: (res) => {
										uni.showToast({
											title: "感谢您的赞助!"
										})
									},
									fail: (res) => {
										uni.showModal({
											content: "支付失败,原因为: " + res.errMsg,
											showCancel: false
										})
									},
									complete: () => {
										this.loading = false;
									}
								})
							} else {
								uni.showModal({
									content: res.data.desc,
									showCancel: false
								})
							}
						},
						fail: (e) => {
							console.log("fail", e);
							this.loading = false;
							uni.showModal({
								content: "支付失败,原因为: " + e.errMsg,
								showCancel: false
							})
						}
					})
				},
				fail: (e) => {
					console.log("fail", e);
					this.loading = false;
					uni.showModal({
						content: "支付失败,原因为: " + e.errMsg,
						showCancel: false
					})
				}
			})
		},
		async requestPayment(e, index) {
			this.providerList[index].loading = true;
			let orderInfo = await this.getOrderInfo(e.id,index);
			console.log(JSON.stringify(orderInfo) );
			if (orderInfo.data.errorCode !== "0000") {
				uni.showModal({
					content: "支付失败",
					showCancel: false
				})
				return;
			}
			uni.requestPayment({
				provider: e.id,
				orderInfo: orderInfo.data.payInfo.payString,
				success: (e) => {
					uni.showToast({
						title: "支付成功"
					})
				},
				fail: (e) => {
					console.log(e.errMsg);
					console.log(JSON.stringify(orderInfo.data.payInfo));
					console.log("fail", e);uni.showModal({
						content: "支付失败",
						showCancel: false
					})
				},
				complete: () => {
					this.providerList[index].loading = false;
				}
			})
		},
		getOrderInfo(e,index) {
			var that = this;
			let appid = "wx726318aa9a4e7971";



			const userInfo ={
					totalFee:that.money,
					body:'商品价格',
			}
			const jsonString ={
				payInfo:userInfo,
				requestType:this.providerList[index].requesttype
			}
			const param ={
				controllerRequestType:"payControllerService",
				jsonString:JSON.stringify(jsonString)
			}
			// let url = 'https://demo.dcloud.net.cn/payment/?payid=' + e + '&appid=' + appid + '&total=0.01';
			return new Promise((res) => {
				uni.request({
					url:that.websiteUrl,
					method:'POST',
					header: {
						// 'Access-Control-Allow-Origin':'*' ,
						"content-type":"application/x-www-form-urlencoded"
					},
					data:param,
					success: (result) => {
						res(result);
					},
					fail: (e) => {
						res(e);
					}
				})
			})
		}
	}
}



